import React from 'react'
import { RequiredLabel } from './RequiredLabel'
import './Input.css'

export const Input = ({ name, label, type='input', onInputChange, required = true }) => {
    // const [inputValue, setInputValue] = useState('')

    const handleInputChange = (e) => {
        // const { value, name } = e.target
        const target = e.target
        // setInputValue(value)
        onInputChange(target)
    }

    const inputProps = {
        required: required ? 'required' : null
    }

    return (
        <div className='inputContainer'>
            <label className='bold' htmlFor={name}> {label} <RequiredLabel required={required} /> </label>
            <input type={type} name={name} id={name} onChange={handleInputChange} {...inputProps} />
        </div>
    )
}
